:root {
  --primary-color: #2c3e50;
  --secondary-color: #3498db;
  --accent-color: #e74c3c;
  --background-color: #f8f9fa;
  --text-color: #333;
  --light-gray: #ecf0f1;
  --white: #fff;
  --shadow: 0 2px 4px #0000001a;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-color);
  background-color: var(--background-color);
  font-family: Roboto, sans-serif;
  line-height: 1.6;
}

.header {
  background-color: var(--white);
  box-shadow: var(--shadow);
  z-index: 1000;
  width: 100%;
  position: fixed;
  top: 0;
}

.site-logo {
  height: 40px;
  margin-right: 18px;
}

.nav-container {
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  display: grid;
  position: relative;
}

.nav-links {
  gap: 2rem;
  display: flex;
}

.logo {
  text-align: center;
  grid-column: 2;
  justify-self: center;
  width: fit-content;
  margin: 0;
}

.logo a {
  color: var(--primary-color);
  cursor: pointer;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: inline-block;
}

.logo h1 {
  color: var(--primary-color);
  margin: 0;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.1;
}

.nav-actions {
  z-index: 2000;
  justify-content: flex-end;
  align-items: center;
  gap: 1.2rem;
  display: flex;
}

.cart-icon {
  z-index: 2000;
  pointer-events: auto;
  align-items: center;
  font-size: 1.5rem;
  transition: transform .2s, color .2s;
  display: flex;
  position: relative;
}

.cart-icon:hover {
  cursor: pointer;
  color: var(--secondary-color);
  transform: scale(1.1);
}

#cart-count, .cart-count {
  color: #fff;
  text-align: center;
  background: #e53935;
  border-radius: 50%;
  min-width: 18px;
  padding: 2px 6px;
  font-size: .8rem;
  position: absolute;
  top: -8px;
  right: -8px;
}

.user-profile {
  align-items: center;
  margin-left: 1rem;
  display: flex;
}

.nav-link {
  color: var(--text-color);
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.nav-link:hover {
  color: var(--secondary-color);
}

.login-button {
  background-color: var(--secondary-color);
  color: var(--white);
  cursor: pointer;
  border-radius: 4px;
  padding: .5rem 1rem;
  transition: background-color .3s;
}

.login-button:hover {
  background-color: #2980b9;
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  background-color: var(--white);
  min-width: 200px;
  box-shadow: var(--shadow);
  z-index: 1001;
  border-radius: 4px;
  padding: .5rem 0;
  display: none;
  position: absolute;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content a {
  color: var(--text-color);
  padding: .8rem 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: block;
}

.dropdown-content a:hover {
  background-color: var(--light-gray);
  color: var(--secondary-color);
}

.hero-carousel {
  height: 70vh;
  min-height: 500px;
  margin-top: 80px;
  display: none;
  position: relative;
  overflow: hidden;
}

.hero-carousel.active {
  display: block;
}

.carousel-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.carousel-slide {
  opacity: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .8s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.carousel-slide.active {
  opacity: 1;
}

.slide-content {
  text-align: center;
  color: #fff;
  backdrop-filter: blur(10px);
  z-index: 10;
  background: #0000004d;
  border-radius: 15px;
  max-width: 600px;
  padding: 2rem;
}

.slide-content h2 {
  text-shadow: 2px 2px 4px #00000080;
  margin-bottom: 1rem;
  font-size: 3rem;
  font-weight: 700;
}

.slide-content p {
  text-shadow: 1px 1px 2px #00000080;
  margin-bottom: 2rem;
  font-size: 1.3rem;
}

.cta-button {
  background: linear-gradient(135deg, var(--primary-color), #0056b3);
  color: #fff;
  cursor: pointer;
  z-index: 20;
  border-radius: 50px;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  position: relative;
  box-shadow: 0 4px 15px #007bff4d;
}

.cta-button:hover {
  background: linear-gradient(135deg, #0056b3, var(--primary-color));
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #007bff66;
}

.carousel-btn {
  color: #fff;
  cursor: pointer;
  backdrop-filter: blur(10px);
  z-index: 10;
  background: #fff3;
  border: none;
  border-radius: 50%;
  padding: 1rem;
  font-size: 2rem;
  transition: all .3s;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.carousel-btn:hover {
  background: #ffffff4d;
  transform: translateY(-50%)scale(1.1);
}

.carousel-btn.prev {
  left: 2rem;
}

.carousel-btn.next {
  right: 2rem;
}

.carousel-dots {
  z-index: 10;
  gap: 1rem;
  display: flex;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.dot {
  cursor: pointer;
  background: #ffffff80;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  transition: all .3s;
}

.dot.active, .dot:hover {
  background: #fff;
  transform: scale(1.2);
}

.logo a:hover {
  text-decoration: none;
}

#main-content {
  opacity: 1;
  max-width: 1200px;
  min-height: calc(100vh - 380px);
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 2rem;
  transition: opacity .3s ease-in-out, transform .5s ease-in-out;
  transform: translateY(0);
}

#main-content.fade-out {
  opacity: 0;
  transform: translateY(-20px);
}

#main-content.slide-in {
  animation: .5s ease-in-out forwards slideDown;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.product-grid {
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
  padding: 2rem 0;
  display: grid;
}

.product-card {
  background-color: var(--white);
  box-shadow: var(--shadow);
  border-radius: 8px;
  transition: transform .3s;
  overflow: hidden;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-image {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.product-info {
  padding: 1rem;
}

.product-title {
  margin-bottom: .5rem;
  font-size: 1.1rem;
  font-weight: 500;
}

.add-to-cart {
  background-color: var(--secondary-color);
  width: 100%;
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin-top: 1rem;
  padding: .8rem;
  transition: background-color .3s;
}

.add-to-cart:hover {
  background-color: #2980b9;
}

.footer {
  background-color: var(--primary-color);
  color: var(--white);
  margin-top: 2rem;
  padding: 3rem 0 1rem;
}

.footer-content {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
}

.footer-section h3 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.social-links {
  gap: 1rem;
  display: flex;
}

.social-link {
  color: var(--white);
  text-decoration: none;
  transition: color .3s;
}

.social-link:hover {
  color: var(--secondary-color);
}

.footer-bottom {
  text-align: center;
  border-top: 1px solid #ffffff1a;
  margin-top: 2rem;
  padding-top: 1rem;
}

.hidden {
  display: none !important;
}

@media (width <= 768px) {
  .nav-container {
    flex-direction: column;
    gap: 1rem;
  }

  .nav-links {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .nav-actions {
    justify-content: center;
    width: 100%;
  }

  .product-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }

  .hero-carousel {
    height: 60vh;
    min-height: 400px;
  }

  .slide-content h2 {
    font-size: 2rem;
  }

  .slide-content p {
    font-size: 1rem;
  }

  .carousel-btn {
    padding: .75rem;
    font-size: 1.5rem;
  }

  .carousel-btn.prev {
    left: 1rem;
  }

  .carousel-btn.next {
    right: 1rem;
  }
}

.auth-container {
  background-color: var(--white);
  max-width: 400px;
  box-shadow: var(--shadow);
  border-radius: 8px;
  margin: 2rem auto;
  padding: 2rem;
}

.auth-form {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.form-group {
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.form-group input {
  border: 1px solid var(--light-gray);
  border-radius: 4px;
  padding: .8rem;
  font-size: 1rem;
}

.cart-container {
  background-color: var(--white);
  box-shadow: var(--shadow);
  border-radius: 8px;
  padding: 2rem;
}

.cart-item {
  border-bottom: 1px solid var(--light-gray);
  grid-template-columns: 100px 1fr auto;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
  display: grid;
}

.cart-item img {
  object-fit: cover;
  border-radius: 4px;
  width: 100px;
  height: 100px;
}

.cart-item-info p {
  color: var(--text-color);
  margin: .5rem 0;
  font-size: 1.1rem;
}

.cart-total {
  text-align: right;
  color: var(--primary-color);
  margin-top: 2rem;
  font-size: 1.3rem;
  font-weight: 700;
}

.payment-form {
  background-color: var(--white);
  max-width: 600px;
  box-shadow: var(--shadow);
  border-radius: 8px;
  margin: 2rem auto;
  padding: 2rem;
}

.payment-form .form-group {
  margin-bottom: 1.5rem;
}

.payment-form label {
  margin-bottom: .5rem;
  font-weight: 500;
}

.payment-form input {
  border: 1px solid var(--light-gray);
  border-radius: 4px;
  width: 100%;
  padding: .8rem;
  font-size: 1rem;
}

.payment-form button {
  background-color: var(--secondary-color);
  width: 100%;
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 1rem;
  font-size: 1.1rem;
  transition: background-color .3s;
}

.payment-form button:hover {
  background-color: #2980b9;
}

.order-summary p {
  color: var(--primary-color);
  margin: 1rem 0;
  font-size: 1.2rem;
  font-weight: 600;
}

.category-header {
  text-align: center;
  background-color: var(--white);
  box-shadow: var(--shadow);
  border-radius: 8px;
  margin-bottom: 2rem;
  padding: 1rem;
}

.category-header h2 {
  color: var(--primary-color);
  margin: 0;
  font-size: 2rem;
}

.google-signin-btn {
  color: #222;
  box-shadow: none;
  cursor: pointer;
  min-width: unset;
  z-index: 1000;
  background: none;
  border: none;
  border-radius: 50px;
  outline: none;
  align-items: center;
  gap: 6px;
  padding: 6px 14px;
  font-size: 1rem;
  font-weight: 500;
  transition: background .15s, color .15s;
  display: flex;
  position: fixed;
  top: 18px;
  right: 32px;
}

.google-signin-btn:hover, .google-signin-btn:focus {
  color: #111;
  background: #f1f1f1;
}

.google-icon {
  width: 20px;
  height: 20px;
  margin-right: 4px;
}

.user-profile-clean {
  background: none;
  align-items: center;
  gap: 10px;
  margin: 0;
  display: flex;
  position: relative;
}

.user-avatar-top {
  cursor: pointer;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  transition: box-shadow .2s;
}

.profile-dropdown {
  background-color: var(--white);
  z-index: 1001;
  border-radius: 8px;
  flex-direction: column;
  align-items: stretch;
  min-width: 180px;
  padding: .5rem 0;
  display: flex;
  position: absolute;
  top: 60px;
  right: 0;
  overflow: hidden;
  box-shadow: 0 8px 16px #0003;
}

.profile-dropdown-item {
  color: var(--text-color);
  white-space: nowrap;
  text-align: left;
  background: none;
  border: none;
  width: 100%;
  padding: .8rem 1.2rem;
  font-size: 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: block;
}

.profile-dropdown-item:hover, .profile-dropdown-item:focus {
  background-color: var(--light-gray);
  color: var(--secondary-color);
}

.nav-link.active, .nav-actions a.active {
  color: var(--primary-color);
  border-bottom: 2px solid var(--primary-color);
  font-weight: 600;
}

.nav-link:hover, .nav-actions a:hover {
  color: var(--primary-color);
  transition: color .3s;
}

.dropdown.active .nav-link {
  color: var(--primary-color);
  font-weight: 600;
}

.breadcrumb {
  background-color: #f8f9fa;
  border-bottom: 1px solid #e9ecef;
  margin-top: 80px;
  padding: .5rem 1rem;
}

.breadcrumb a {
  color: #6c757d;
  font-size: .9rem;
  text-decoration: none;
}

.breadcrumb a:hover {
  color: var(--primary-color);
}

.breadcrumb a:not(:last-child):after {
  content: " > ";
  color: #6c757d;
  margin: 0 .5rem;
}

.loading {
  height: 200px;
  color: var(--primary-color);
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  display: flex;
}

.loading:after {
  content: "";
  border: 2px solid #f3f3f3;
  border-top: 2px solid var(--primary-color);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin-left: 10px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error-page {
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.error-page h2 {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.error-page p {
  color: #6c757d;
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.error-page .btn {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: 5px;
  padding: .75rem 1.5rem;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.error-page .btn:hover {
  background-color: #0056b3;
}

.products-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.products-section h2 {
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2rem;
}

.products-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  padding: 1rem;
  display: grid;
}

.product-card {
  background: #fff;
  border-radius: 12px;
  transition: transform .3s, box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 4px 15px #0000001a;
}

.product-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 200px;
}

.product-details {
  padding: 1.5rem;
}

.product-details .price {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-size: 1.3rem;
  font-weight: 600;
  display: block;
}

.add-to-cart-btn {
  background: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  width: 100%;
  padding: .75rem;
  font-weight: 500;
  transition: background-color .3s;
}

.add-to-cart-btn:hover {
  background: #0056b3;
}

@media (width <= 768px) {
  .products-section {
    padding: 1rem;
  }

  .products-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .product-card {
    max-width: 400px;
    margin: 0 auto;
  }
}

.hero-section {
  background: #f8f9fa;
  width: 100vw;
  min-height: 60vh;
  margin-top: 80px;
  position: relative;
  box-shadow: 0 4px 24px #2c3e5014;
}

.layered-section {
  background: linear-gradient(90deg, #f8f9fa 60%, #eaf1f8 100%);
  width: 100vw;
  max-width: 100%;
  margin-bottom: 0;
  padding: 64px 0;
  box-shadow: 0 2px 16px #2c3e500a;
}

.about-content, .contact-content {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 48px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.about-text, .contact-text {
  flex: 350px;
  min-width: 300px;
}

.about-image, .contact-image {
  flex: 350px;
  justify-content: center;
  min-width: 300px;
  display: flex;
}

.about-image img, .contact-image img {
  object-fit: cover;
  border-radius: 18px;
  width: 100%;
  max-width: 400px;
  height: auto;
  box-shadow: 0 4px 24px #2c3e501f;
}

.products-preview-section {
  background: #fff;
  padding: 64px 0;
  box-shadow: 0 2px 16px #2c3e500a;
}

.products-preview-section h2 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 40px;
  font-size: 2.4rem;
}

.products-preview-grid {
  justify-content: center;
  gap: 32px;
  padding: 0 32px;
  display: flex;
  overflow-x: auto;
}

.product-card {
  background: #fff;
  border-radius: 18px;
  flex-direction: column;
  flex: 0 0 320px;
  align-items: stretch;
  min-width: 320px;
  max-width: 340px;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 2px 16px #2c3e501a;
}

.product-card:hover {
  transform: translateY(-8px)scale(1.03);
  box-shadow: 0 8px 32px #2c3e502e;
}

.product-image {
  background-position: center;
  background-size: cover;
  border-radius: 18px 18px 0 0;
  width: 100%;
  height: 200px;
}

.product-details {
  flex-direction: column;
  gap: 10px;
  padding: 24px 20px 20px;
  display: flex;
}

.product-details h3 {
  color: #2c3e50;
  margin-bottom: 4px;
  font-size: 1.2rem;
}

.product-details p {
  color: #555;
  margin-bottom: 8px;
  font-size: 1rem;
}

.product-details .price {
  color: #3498db;
  margin-bottom: 12px;
  font-size: 1.1rem;
  font-weight: 700;
}

.add-to-cart-btn {
  color: #fff;
  cursor: pointer;
  background: #2c3e50;
  border: none;
  border-radius: 8px;
  padding: 12px 0;
  font-size: 1rem;
  font-weight: 500;
  transition: background .2s, transform .15s;
}

.add-to-cart-btn:hover, .add-to-cart-btn:focus {
  color: #fff;
  background: #3498db;
  transform: scale(1.05);
}

.hero-section, .layered-section {
  margin-bottom: 0;
}

@media (width <= 900px) {
  .about-content, .contact-content {
    flex-direction: column;
    gap: 32px;
  }

  .about-image img, .contact-image img {
    max-width: 90vw;
  }

  .products-preview-grid {
    gap: 16px;
    padding: 0 8px;
  }
}

@media (width <= 600px) {
  .hero-section {
    min-height: 40vh;
  }

  .products-preview-section h2 {
    font-size: 1.5rem;
  }

  .product-card {
    min-width: 90vw;
    max-width: 95vw;
  }

  .about-text, .contact-text {
    min-width: 0;
  }
}

.cta-button, .add-to-cart-btn, .checkout-button, .btn {
  cursor: pointer;
  transition: background .2s, color .2s, transform .15s, box-shadow .15s, text-decoration .2s;
}

.cta-button:hover, .add-to-cart-btn:hover, .checkout-button:hover, .btn:hover {
  color: #fff;
  background: #2980b9;
  text-decoration: underline;
  transform: scale(1.05);
  box-shadow: 0 2px 8px #2c3e501a;
}

.nav-link, .dropdown-content a {
  cursor: pointer;
  transition: color .2s, text-decoration .2s;
}

.nav-link:hover, .dropdown-content a:hover {
  color: var(--secondary-color);
  text-decoration: underline;
